<template>
  <div ref="animation" :class="['form',form?'animation_right_start':'animation_opacity']">
    <div class="form_item">
      <div class="form_item_label">您的姓名</div>
      <div class="item_input">
        <input type="text" placeholder="请输入您的姓名" v-model="franchise_form.username">
        <span>*</span>
      </div>
    </div>
    <div class="form_item">
      <div class="form_item_label">联系方式</div>
      <div class="item_input">
        <input type="text" placeholder="请输入您的联系方式" v-model="franchise_form.contactInfo">
        <span>*</span>
      </div>
    </div>
    <div class="form_item">
      <div class="form_item_label">联系地址</div>
      <div class="item_input">
        <input type="text" placeholder="请输入您的联系地址" v-model="franchise_form.address">
        <span>*</span>
      </div>
    </div>
    <div class="form_item">
      <div class="form_item_label">合作需求</div>
      <div class="item_input">
        <textarea placeholder="我们能为您做什么" v-model="franchise_form.cooperation"></textarea>
        <span>*</span>
      </div>
    </div>
    <div class="form_item item_code">
      <div class="form_item_label">验证码</div>
      <div class="item_input">
        <input type="text" class="code" v-model="franchise_form.code">
        <ImgVerify ref="verifyRef"></ImgVerify>
        <span class="change">换一张</span>
        <span>*</span>
      </div>
    </div>
    <div class="check_message" v-text="check_message">
    </div>
    <div class="submit" @click="submit">
      提交
    </div>
  </div>
</template>

<script>
import ImgVerify from '@/components/ImgVerify'
import { mixins } from '@/mixin'
export default {
  name:'franchiseform',
  mixins:[ mixins ],
  components:{ ImgVerify },
  data() {
    return ({
      franchise_form: {
        username: '',
        contactInfo: '',
        address: '',
        cooperation: '',
        code: ''
      },
      check_message: ''
    })
  },
  watch: {
    franchise_form: {
      handler(oldVal, newVal) {
        if (newVal.code != '') this.check_message = ''
      },
      deep: true
    }
  },
  methods: {
    submit() {
      if (!this.franchise_form.username) {
        this.check_message = '请输入“您的姓名”'
        return
      }
      if (!this.franchise_form.contactInfo) {
        this.check_message = '请输入“联系方式”'
        return
      }
      if (!this.franchise_form.address) {
        this.check_message = '请输入“联系地址”'
        return
      }
      if (!this.franchise_form.cooperation) {
        this.check_message = '请输入“合作方式”'
        return
      }
      if (!this.franchise_form.code) {
        this.check_message = '请输入“验证码”'
        return
      }
    }
  }
}
</script>

<style scoped lang="scss">
.form {
  .item_code {
    width: 350px !important;
    .code{
      flex: 0!important;
      width: 121px;
    }
  }

  .check_message {
    margin: 15px 0;
    text-align: center;
    color: #ff0000;
  }

  .form_item {
    width: 480px;
    // height: 49px;
    margin-bottom: 9px;

    .form_item_label {
      color: #666666;
      margin-bottom: 8px;
      font-size: 14px;
      font-weight: 700;
    }

    .item_input {
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: rgb(102, 102, 102);

      .change {
        color: rgb(102, 102, 102);
      }

      textarea {
        height: 101px;
        line-height: 26px;
      }

      input {
        height: 35px;
      }

      input,
      textarea {
        flex: 1;
        border: 1px solid #dadada;
        border-radius: 2px;
        text-indent: 6px;
        font-size: 12px;
        color: #666666;
      }

      span {
        color: #ff0000;
        margin-left: 12px;
        font-size: 14px;
      }
    }
  }

  .submit {
    width: 168px;
    height: 44px;
    text-align: center;
    line-height: 44px;
    color: #fff;
    background: #5ab4ff;
    font-size: 14px;
    text-align: center;
    margin-top: 20px;
    cursor: pointer;
  }

}
</style>